* {
  /* box-sizing: border-box; */
  margin: 0;
  font-family: "Ubuntu", sans-serif;
}

body {
  background-color: rgb(0, 220, 150);
}

.firstdiv {
  box-shadow: #00568847 3px 3px 3px 3px;
  border-radius: 50px;
  width: 303px;
  height: auto;
  padding: 30px;
  display: flex;
  justify-content: center;
  margin: 80px auto;
  background-color: white;
}

.seconddiv {
  display: inline-block;
}
.h3 {
  margin-top: 5px !important;
  text-align: center;
  text-shadow: #00568847 2px 4px 2px;
}
.button {
  height: 60px;
  border-radius: 50% !important;
  position: relative !important;
  bottom: -24px;
  left: 205px;
}

.input {
  position: relative !important;
  bottom: -35px;
  right: 70px;
}

li {
  list-style: none;
  color: #1976d2;
  position: relative;
  left: 30px;
  bottom: 1px;
}

span {
  position: absolute;
}

.delete {
  color: #1976d2;
}

.delete:hover {
  color: red;
}
